html {
  margin: 0;
  padding: 0;
  font-family: 'DINPro', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  min-height: 100%;
  display: flex;
}
html {
  font-family: 'Open Sans', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  background: #000 url("img/bg.jpg") 50% 50% no-repeat;
  background-size: cover;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
}
.front-page {
  background: #fafafa;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  color: inherit;
  text-decoration: none;
}
button {
  color: inherit;
}
.line:after {
  content: '';
  display: block;
  width: 120px;
  height: 6px;
  margin: 10px auto;
  background: linear-gradient(to right, #e94c36, rgba(0,0,0,0));
}
.title:after {
  content: '';
  display: block;
  width: 120px;
  height: 6px;
  margin: 10px auto;
  background: linear-gradient(to right, #e94c36, rgba(0,0,0,0));
}
.title {
  font-size: 48px;
  line-height: 1;
  font-weight: 400;
  margin: 0 0 50px;
}
.title:after {
  height: 4px;
}
@media (max-width: 767px) {
  .title {
    font-size: 32px;
  }
}
.advantages {
  background-color: #fff;
  padding: 50px 0;
}
.advantages .title__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media (max-width: 991px) {
  .advantages .title__wrapper {
    justify-content: center;
  }
}
.advantages .title__wrapper .advantages__title {
  font-weight: bold;
  font-size: 24px;
}
.advantages__items {
  margin-bottom: 55px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 991px) {
  .advantages__items {
    flex-direction: column;
    align-items: center;
  }
}
.advantages__items .advantages__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (max-width: 991px) {
  .advantages__items .advantages__item {
    align-items: center;
    margin-bottom: 20px;
  }
}
.advantages__items .advantages__item .advantages__image {
  background-color: #0e3264;
  border-radius: 50%;
  width: 65px;
  height: 66px;
  display: flex;
  justify-content: center;
}
.advantages__items .advantages__item .advantages__name {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 21px;
  margin-top: 30px;
  margin-bottom: 30px;
  color: #000;
}
.advantages__items .advantages__item .advantages__text {
  font-size: 16px;
}
@media (max-width: 991px) {
  .advantages__items .advantages__item .advantages__text {
    text-align: center;
  }
}
.header {
  background: url("modules/uralsib/header/header-bg.png");
  background-size: cover;
  color: #000;
}
.header .w {
  display: flex;
  padding: 50px 10px;
}
@media (max-width: 991px) {
  .header .w {
    flex-direction: column;
    align-items: center;
  }
  .header .w .header__form-wrapper {
    margin-top: 30px;
  }
}
.header #settlement {
  display: none;
}
.header #street {
  display: none;
}
@media (max-width: 767px) {
  .header__top {
    text-align: center;
  }
}
.header__content {
  flex: 1;
  padding: 0;
  padding-right: 30px;
  text-align: left;
