    background: #fff;
    flex-direction: column;
    margin: 7px;
    padding: 17px 22px;
  }
}
@media (max-width: 767px) {
  .table__cell {
    display: flex;
    min-width: 100%;
    margin-top: 12px;
  }
  .table__cell:first-child {
    margin-top: 0;
  }
}
.table__label {
  display: none;
  min-width: 120px;
}
@media (max-width: 767px) {
  .table__label {
    display: block;
  }
}
.table__value {
  flex: 1;
}
.table__value a {
  color: #227eba;
  text-decoration: underline;
}
.table__value a:hover {
  text-decoration: none;
}
.table__number {
  width: 50px;
}
.table__address {
  flex: 1;
  padding-right: 12px;
}
.table__date {
  width: 150px;
}
.table__certificate {
  width: 120px;
}
.table__template {
  display: none;
}
.partners {
  background: #227dba;
  padding: 25px 0 90px;
  color: #fff;
}
.partners__title {
  text-align: center;
  font-size: 22px;
  font-weight: 300;
  margin-bottom: 35px;
}
.partners__block {
  display: flex;
  align-items: center;
  width: 100%;
}
@media (max-width: 991px) {
  .partners__block {
    display: block;
  }
}
.partners__slider {
  flex: 1;
  overflow: hidden;
}
.partners__button {
  text-align: center;
  max-width: 200px;
  margin-left: 15px;
}
@media (max-width: 991px) {
  .partners__button {
    max-width: none;
    margin-top: 15px;
    margin-left: 0;
  }
}
.partners-slider {
  position: relative;
  padding: 0 20px;
  overflow: hidden;
}
.partners-slider img {
  display: block;
  margin: 0 auto;
  width: auto !important;
}
.partners-slider .owl-carousel {
  width: 100%;
}
.partners-slider .owl-prev,
.partners-slider .owl-next {
  position: absolute;
  top: 50%;
  z-index: 1;
  width: 20px;
  height: 50px;
  margin-top: -16px;
  opacity: 0.2;
  transition: all 0.1s ease-out;
}
.partners-slider .owl-prev:hover,
.partners-slider .owl-next:hover {
  opacity: 0.7;
}
.partners-slider .owl-prev {
  background: url("img/sprite-arrow-left.svg");
  right: 100%;
}
.partners-slider .owl-next {
  background: url("img/sprite-arrow-right.svg");
  left: 100%;
}
.profile-table {
  width: 100%;
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 21px 32px rgba(35,60,76,0.05);
  font-size: 12px;
}
@media (max-width: 767px) {
  .profile-table {
    background-color: transparent;
    padding: 0;
    box-shadow: none;
  }
}
.profile-table__template {
  display: none;
}
.profile-table__label {
  display: none;
  min-width: 120px;
  max-width: 120px;
  font-size: 11px;
}
@media (max-width: 767px) {
  .profile-table__label {
    display: block;
  }
}
@media (max-width: 767px) {
  .profile-table__item {
    margin: 7px;
    border-radius: 4px;
    overflow: hidden;
  }
}
.profile-table__header,
.profile-table__body {
  padding: 0 12px;
}
.profile-table__header,
.profile-table__row {
  display: flex;
}
.profile-table__header:after,
.profile-table__row:after {
  content: '';
  display: table;
  clear: both;
}
.profile-table__header {
  height: 53px;
  align-items: center;
  border-bottom: 1px solid rgba(151,151,151,0.2);
}
@media (max-width: 767px) {
  .profile-table__header {
    display: none;
  }
}
.profile-table__body {
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (max-width: 767px) {
  .profile-table__body {
    padding: 0;
  }
}
.profile-table__row {
  padding: 12px 0;
  cursor: pointer;
}
.profile-table__row:hover,
.profile-table__row--active {
  background: #f5f2eb;
  transition: all 0.1s ease-out;
}
@media (max-width: 767px) {
  .profile-table__row {
    background: #fff;
    flex-direction: column;
    padding: 17px 22px;
    position: relative;
  }
}
@media (max-width: 767px) {
  .profile-table__cell {
    display: flex;
    min-width: 100%;
    margin-top: 12px;
  }
  .profile-table__cell:first-child {
    margin-top: 0;
  }
}
.profile-table__info {
  background: #fbfbfb;
  display: flex;
  padding: 18px 0 18px 90px;
  color: #505050;
}
@media (max-width: 767px) {
  .profile-table__info {
    padding: 17px 22px;
    flex-direction: column;
  }
}
.profile-table__info-wrapper {
  max-height: 0;
  overflow: hidden;
  transition: all 0.1s ease-out;
}
.profile-table__info-col {
  flex: 1;
}
.profile-table__info-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 14px;
}
.profile-table__info-label {
  width: 30%;
  min-width: 200px;
  font-size: 13px;
}
@media (max-width: 767px) {
  .profile-table__info-label {
    min-width: auto;
    max-width: 120px;
    font-size: 11px;
  }
}
.profile-table__info-value {
  font-size: 14px;
}
@media (max-width: 767px) {
  .profile-table__info-value {
    font-size: 12px;
  }
}
.profile-table__button {
  vertical-align: bottom;
  border: 1px solid #898989;
  background: transparent;
  color: #898989;
  height: 20px;
  line-height: 18px;
  display: inline-block;
  border-radius: 4px;
  font-size: 11px;
  font-weight: bold;
  text-align: center;
  padding: 0 5px;
  cursor: pointer;
  outline: 0;
  transition: all 0.15s ease-out;
}
.profile-table__button:hover {
  border: 1px solid #898989;
  background: #898989;
  color: #fff;
}
.profile-table__button.active {
  color: transparent;
  position: relative;
  overflow: hidden;
}
.profile-table__button.active .progress {
  display: block;
}
.profile-table__button .progress {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background: #e94c36;
  transition: all 0.1s ease-out;
}
.profile-table__toggle {
  width: 30px;
  position: relative;
}
@media (max-width: 767px) {
  .profile-table__toggle {
    position: static;
  }
}
.profile-table__toggle:after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 5px;
  transform: translateX(-50%);
  background: url("img/sprite-toggle.svg");
  transition: all 0.1s ease-out;
}
.profile-table__row--active .profile-table__toggle:after {
  transform: translateX(-50%) rotate(180deg);
}
.profile-table__header .profile-table__toggle:after {
  display: none;
}
@media (max-width: 767px) {
  .profile-table__toggle:after {
    left: auto;
    right: 8px;
    top: 16px;
  }
}
.profile-table__number {
  width: 75px;
}
.profile-table__address {
  flex: 1;
  padding-right: 12px;
}
.profile-table__date {
  width: 150px;
}
.profile-table__show {
  width: 130px;
}
.profile-table__status {
  width: 120px;
}
.profile-table__payment {
  width: 100px;
}
.profile-table__documents {
  width: 150px;
}
.profile-table__buttons {
  width: 150px;
  text-align: right;
}
.profile-table__buttons {
  width: 105px;
  text-align: right;
}
.profile-table__buttons * + * {
  margin-top: 5px;
}
.invoice {
  position: absolute;
  left: 8px;
  top: 16px;
  right: 8px;
  bottom: 104px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.invoice__image {
  max-height: 100%;
  display: block;
  margin: 0 auto;
}
.invoice__form {
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
}
.static-page {
  margin-top: 24px;
  width: 100%;
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 21px 32px rgba(35,60,76,0.05);
  overflow: auto;
  font-size: 14px;
  padding: 22px 44px;
}
